import React, { useState } from "react";
import "rsuite/dist/styles/rsuite-default.css";
import { Button } from "rsuite";
import { Tree, Icon, InputGroup, Input } from "rsuite";
import { testData } from "./testdata.js";
function RSuiteTest(props) {
  const [searchKeyword, setSearchKeyword] = useState("");
  const [treeValue, setTreeValue] = useState(22);
  return (
    <div>
      <InputGroup {...props} inside>
        <Input
          placeholder="Search"
          type="text"
          onChange={(value) => setSearchKeyword(value)}
          value={searchKeyword}
        />
        {/* <InputGroup.Button>
          <Icon icon="search" />
        </InputGroup.Button> */}

        <InputGroup.Button onClick={() => setSearchKeyword("")}>
          <Icon icon="close" />
        </InputGroup.Button>
      </InputGroup>

      <Tree
        data={testData}
        defaultExpandAll={false}
        defaultExpandItemValues={[1, treeValue]}
        searchKeyword={searchKeyword}
        onSelect={(e) => {
          console.log(e);
          setTreeValue(e.value);
        }}
        onExpand={(e) => console.log(e)}
        value={treeValue}
        disabledItemValues={[5]}
      />
    </div>
  );
}

export default RSuiteTest;
