import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import TreeView from "@material-ui/lab/TreeView";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import TreeItem from "@material-ui/lab/TreeItem";
import _ from "lodash";

const useStyles = makeStyles({
  root: {
    height: 216,
    flexGrow: 1,
    maxWidth: 400,
  },
});

export default function ControlledTreeView(props) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState([]);
  const [selected, setSelected] = React.useState([]);
  const [searchValue, setSearchValue] = React.useState("");
  const [stateData, setStateData] = React.useState(undefined);

  React.useEffect(() => {
    const { data } = props;
    setStateData(data);
  }, [props]);

  const handleToggle = (event, nodeIds) => {
    setExpanded(nodeIds);
  };

  const handleSelect = (event, nodeIds) => {
    setSelected(nodeIds);
  };

  const onSearch = (search) => {
    search = "PDF";
    setSearchValue(search);
    console.log(searchTree(search, stateData));
  };

  function searchTree(str, data) {
    console.log("searchTree() ", str, data);
    const searchStr = str.toLowerCase();

    // Only return the entries that contain a matched value
    return _.filter(data, (datum) => {
      // Check if name matches
      return (
        _.includes(datum.name, searchStr) ||
        _.some(datum.activities, (activity) => {
          return _.entries(activity.routines).some(([routine, { details }]) => {
            // Check if dynamic routine matches or details
            return (
              _.includes(routine, searchStr) || _.includes(details, searchStr)
            );
          });
        })
      );
    });
  }

  const renderTree = (nodes) => (
    <TreeItem key={nodes.id} nodeId={nodes.id} label={nodes.name}>
      {Array.isArray(nodes.children)
        ? nodes.children.map((node) => renderTree(node))
        : null}
    </TreeItem>
  );

  return (
    <div>
      <button onClick={() => onSearch("a")}>Search</button>
      <TreeView
        className={classes.root}
        defaultCollapseIcon={<ExpandMoreIcon />}
        defaultExpanded={["root"]}
        defaultExpandIcon={<ChevronRightIcon />}
      >
        {renderTree(stateData)}
      </TreeView>
    </div>
  );
}
