import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TreeView from "@material-ui/lab/TreeView";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import TreeItem from "@material-ui/lab/TreeItem";
import _ from "lodash";

const useStyles = makeStyles({
  root: {
    height: 110,
    flexGrow: 1,
    maxWidth: 400,
  },
});

export default function MaterialUiTreeView(props) {
  const classes = useStyles();
  const [selected, setSelected] = useState("");
  const [searchKeyword, setSearchKeyword] = useState("3");

  const [data, setData] = useState({
    id: "root",
    name: "Parent",
    children: [
      {
        id: "1",
        name: "Child - 1",
        children: [
          {
            id: "2",
            name: "Child - 2",
          },
        ],
      },
      {
        id: "3",
        name: "Child - 3",
        children: [
          {
            id: "4",
            name: "Child - 4",
          },
          {
            id: "5",
            name: "Child - 5",
          },
        ],
      },
    ],
  });
  const renderTree = (nodes) => (
    <TreeItem key={nodes.id} nodeId={nodes.id} label={nodes.name}>
      {Array.isArray(nodes.children)
        ? nodes.children.map((node) => renderTree(node))
        : null}
    </TreeItem>
  );

  const filterData = (searchKeyword) => {
    console.log("filterData", data, searchKeyword);

    let filteredData = { ...data };
    if (searchKeyword.length > 0) {
      filteredData = _.filter(data, function (o) {
        return !o.name.toLowerCase().includes(searchKeyword);
      });
    }
    setData(filteredData);
  };

  return (
    <>
      <button onClick={filterData}>Search</button>
      <TreeView
        className={classes.root}
        defaultCollapseIcon={<ExpandMoreIcon />}
        defaultExpanded={["root"]}
        defaultExpandIcon={<ChevronRightIcon />}
        onNodeSelect={(e, value) => {
          console.log(e, value);
          setSelected(value);
        }}
        selected={selected}
      >
        {renderTree(data)}
      </TreeView>
    </>
  );
}
